:root {
  --terminal-bg: #1e1e1e;
  --terminal-text: #f0f0f0;
  --terminal-green: #4af626;
  --terminal-blue: #00a8ff;
  --terminal-yellow: #f9f871;
  --terminal-red: #ff5555;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Courier New', monospace;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

*::-webkit-scrollbar {
  display: none;
}

body, html {
  height: 100%;
  overflow: hidden;
}

.App {
  background-color: var(--terminal-bg);
  color: var(--terminal-text);
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.matrix-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  opacity: 0.05;
  pointer-events: none;
}

.matrix-container canvas {
  display: block;
  width: 100%;
  height: 100%;
}

.terminal-window {
  background-color: var(--terminal-bg);
  width: 100%;
  height: 100%;
  max-width: 900px;
  margin: 20px auto;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  z-index: 2;
}

.terminal-header {
  background: linear-gradient(to right, #333, #444);
  padding: 10px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  display: flex;
  align-items: center;
  gap: 10px;
  flex-shrink: 0;
}

.terminal-buttons {
  display: flex;
  gap: 8px;
}

.terminal-button {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  cursor: pointer;
}

.terminal-button.close {
  background-color: #ff5f56;
}

.terminal-button.minimize {
  background-color: #ffbd2e;
}

.terminal-button.maximize {
  background-color: #27c93f;
}

.terminal-title {
  flex-grow: 1;
  text-align: center;
  font-size: 14px;
  color: #aaa;
}

.terminal-body {
  flex-grow: 1;
  padding: 15px;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #121212;
  height: calc(100% - 95px);
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.terminal-body::-webkit-scrollbar {
  display: none;
}

.terminal-input-line {
  display: flex;
  padding: 10px;
  background: linear-gradient(to right, #333, #444);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  flex-shrink: 0;
  position: relative;
  bottom: 0;
  width: 100%;
}

.terminal-prompt {
  color: var(--terminal-green);
  margin-right: 10px;
  white-space: nowrap;
}

.username {
  color: var(--terminal-blue);
}

.prompt-path {
  color: #ff79c6;
}

.terminal-input {
  background: transparent;
  border: none;
  color: var(--terminal-text);
  outline: none;
  font-size: 16px;
  width: 100%;
  caret-color: var(--terminal-text);
}

.terminal-output {
  margin-bottom: 15px;
  word-wrap: break-word;
  max-width: 100%;
}

.command-output {
  margin-top: 5px;
  max-width: 100%;
}

pre {
  white-space: pre-wrap;
  word-wrap: break-word;
  max-width: 100%;
  overflow-x: hidden;
}

.ascii-art {
  color: var(--terminal-blue);
  font-size: 0.8em;
  line-height: 1.2;
  letter-spacing: 1px;
}

.help-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 10px;
  margin-top: 10px;
}

.help-item {
  background-color: #252525;
  padding: 8px;
  border-radius: 4px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.help-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.command {
  color: var(--terminal-green);
  font-weight: bold;
}

.description {
  color: #ccc;
  font-size: 0.9em;
  margin-top: 4px;
}

.highlight {
  color: var(--terminal-blue);
  font-weight: bold;
}

.error {
  color: var(--terminal-red);
}

.loading-dots {
  display: inline-block;
  margin-left: 8px;
}

.loading-dots span {
  animation: loading 1.4s infinite ease-in-out both;
  display: inline-block;
  width: 4px;
  height: 4px;
  background-color: var(--terminal-green);
  border-radius: 50%;
  margin-right: 2px;
}

.loading-dots span:nth-child(1) {
  animation-delay: -0.32s;
}

.loading-dots span:nth-child(2) {
  animation-delay: -0.16s;
}

@keyframes loading {
  0%, 80%, 100% { 
    transform: scale(0);
  }
  40% { 
    transform: scale(1);
  }
}

.project-item {
  background-color: #252525;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 15px;
  transition: all 0.3s ease;
}

.project-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.tech-tag, .skill-tag {
  display: inline-block;
  background-color: #333;
  color: var(--terminal-green);
  padding: 4px 8px;
  border-radius: 4px;
  margin: 4px;
  font-size: 0.8em;
}

@media (max-width: 768px) {
  .terminal-window {
    margin: 0;
    border-radius: 0;
    height: 100vh;
    max-width: 100%;
    width: 100%;
  }
  
  .terminal-header {
    padding: 8px;
  }
  
  .terminal-button {
    width: 12px;
    height: 12px;
  }
  
  .terminal-title {
    font-size: 12px;
  }
  
  .terminal-body {
    padding: 10px;
    height: calc(100vh - 120px);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  
  .terminal-input-line {
    padding: 8px;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(to right, #333, #444);
    z-index: 1000;
  }
  
  .terminal-input {
    font-size: 14px;
    padding: 8px;
    height: 40px;
  }
  
  .help-grid {
    grid-template-columns: 1fr;
    gap: 8px;
  }
  
  .ascii-art {
    font-size: 0.7em;
    overflow-x: auto;
    white-space: pre;
  }
  
  .project-item, .experience-item, .education-item, .achievement-item {
    padding: 12px;
    margin-bottom: 10px;
  }
  
  .project-title, .job-title, .degree-title, .achievement-title {
    font-size: 1.1em;
  }
  
  .project-description, .job-period, .institution, .period, .achievement-description {
    font-size: 0.9em;
  }
  
  .tech-stack, .skill-items {
    flex-wrap: wrap;
    gap: 6px;
  }
  
  .tech-tag, .skill-tag {
    font-size: 0.75em;
    padding: 3px 6px;
  }
  
  .skill-category {
    margin-bottom: 15px;
  }
  
  .category-title {
    font-size: 1em;
    margin-bottom: 8px;
  }
  
  .info-section {
    margin-bottom: 15px;
  }
  
  .info-row {
    font-size: 0.9em;
    margin-bottom: 8px;
  }
  
  .description-section p {
    font-size: 0.9em;
    line-height: 1.4;
  }
  
  .section-title {
    font-size: 1.2em;
    margin-bottom: 12px;
  }
  
  .section-subtitle {
    font-size: 1em;
    margin-bottom: 10px;
  }
  
  .contact-info {
    margin-top: 15px;
  }
  
  .contact-item {
    margin-bottom: 10px;
  }
  
  .social-links {
    margin-top: 15px;
  }
  
  .social-item {
    margin-bottom: 10px;
  }
  
  .neofetch-container {
    flex-direction: column;
  }
  
  .neofetch-ascii {
    margin-right: 0;
    margin-bottom: 20px;
  }
  
  .neofetch-info {
    width: 100%;
  }
  
  .neofetch-row {
    margin-bottom: 8px;
  }
  
  .chat-container {
    padding: 15px;
    margin-bottom: 60px;
  }
  
  .chat-header {
    margin-bottom: 15px;
  }
  
  .chat-title {
    font-size: 1.2em;
  }
  
  .chat-subtitle {
    font-size: 0.9em;
  }
  
  .chat-hint {
    font-size: 0.8em;
  }
  
  .ai-chat-message {
    padding: 12px;
    margin-bottom: 12px;
  }
  
  .user-question {
    font-size: 0.9em;
  }
  
  .ai-response {
    font-size: 0.9em;
  }
  
  .recruiter-popup {
    width: 95%;
    max-height: 90vh;
    margin: 10px;
    padding: 15px;
  }
  
  .recruiter-popup-title {
    font-size: 1.1em;
  }
  
  .recruiter-popup-content {
    font-size: 0.95em;
  }
  
  .recruiter-popup-content p {
    margin-bottom: 12px;
  }
  
  .recruiter-popup-content .command,
  .recruiter-popup-content .tip {
    padding: 4px 8px;
    margin: 4px 0;
  }
}

@media (max-width: 360px) {
  .terminal-title {
    font-size: 10px;
  }
  
  .terminal-input {
    font-size: 12px;
    height: 36px;
  }
  
  .ascii-art {
    font-size: 0.6em;
  }
  
  .project-title, .job-title, .degree-title, .achievement-title {
