}

.recruiter-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--terminal-bg);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5),
              0 0 0 1px rgba(0, 168, 255, 0.2);
  z-index: 1000;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
  border: 1px solid var(--terminal-blue);
  transition: all 0.3s ease;
  font-family: 'Courier New', monospace;
}

.recruiter-popup.closing {
  opacity: 0;
  transform: translate(-50%, -60%);
  pointer-events: none;
}

.recruiter-popup-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 1px solid var(--terminal-blue);
  padding-bottom: 15px;
  position: sticky;
  top: 0;
  background-color: var(--terminal-bg);
  z-index: 1;
  padding-right: 5px;
}

.recruiter-popup-title {
  color: var(--terminal-blue);
  font-size: 1.2em;
  font-weight: bold;
  margin: 0;
  flex: 1;
  text-align: left;
  text-shadow: 0 0 5px rgba(0, 168, 255, 0.3);
}

.recruiter-popup-close {
  background: none;
  border: none;
  color: var(--terminal-text);
  font-size: 1.2em;
  cursor: pointer;
  padding: 5px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border-radius: 4px;
  margin-left: 10px;
  flex-shrink: 0;
  position: relative;
}

.recruiter-popup-close:hover {
  color: var(--terminal-red);
  background-color: rgba(255, 85, 85, 0.1);
  transform: scale(1.1);
}

.recruiter-popup-content {
  color: var(--terminal-text);
  line-height: 1.6;
  padding: 5px 0;
}

.recruiter-popup-content p {
  margin-bottom: 15px;
  padding: 0 5px;
}

.recruiter-popup-content .highlight {
  color: var(--terminal-blue);
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 168, 255, 0.2);
}

.recruiter-popup-content .command {
  color: var(--terminal-green);
  font-family: monospace;
  padding: 3px 6px;
  background-color: rgba(0, 168, 255, 0.1);
  border-radius: 4px;
  border: 1px solid var(--terminal-blue);
  display: inline-block;
  margin: 3px 0;
  box-shadow: 0 0 5px rgba(0, 168, 255, 0.1);
}

.recruiter-popup-content .tip {
  color: var(--terminal-yellow);
  font-style: italic;
  background-color: rgba(249, 248, 113, 0.1);
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid var(--terminal-yellow);
  margin: 8px 0;
  box-shadow: 0 0 5px rgba(249, 248, 113, 0.1);
}

.recruiter-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 999;
  transition: opacity 0.3s ease;
  backdrop-filter: blur(2px);
}

.avatar-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
