}

.funny-statement {
  position: absolute;
  bottom: 100%;
  right: 0;
  background-color: #252525;
  color: var(--terminal-text);
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 10px;
  max-width: 200px;
